
/**
 * h1,h2,h3ã«è©²å½“ã™ã‚‹`#`ã‚’å‰Šé™¤ã—ã€ãã‚Œã«è¦‹åˆã†Tabã‚’è£œæ­£ã™ã‚‹
 * @param longText textareaå†…ã®æ–‡å­—åˆ—
 * @return æ–°ã—ã„ãƒ†ã‚­ã‚¹ãƒˆ
 */
const sharpFormat = (longText: string) => {
    const oldlines = nSplit(longText);
    // 1è¡Œã”ã¨ã«ç¢ºèª
    // ã‚‚ã—###ãŒã‚ã‚‹â†’è©²å½“è¡Œã¨ãã‚Œä»¥é™ã¯h3ã‚°ãƒ«ãƒ¼ãƒ—(è¦‹å‡ºã—tab2+æœ¬æ–‡tab3)
    // ã‚‚ã—##ãŒã‚ã‚‹â†’è©²å½“è¡Œã¨ãã‚Œä»¥é™ã¯h2ã‚°ãƒ«ãƒ¼ãƒ—(è¦‹å‡ºã—tab1+æœ¬æ–‡tab1)
    // ã‚‚ã—#ãŒã‚ã‚‹â†’è©²å½“è¡Œã¨ãã‚Œä»¥é™ã¯h1ã‚°ãƒ«ãƒ¼ãƒ—(è¦‹å‡ºã—tab0+æœ¬æ–‡tab1)
    // #ãŒç„¡ã„â†’å‰è¡Œã®çŠ¶æ…‹ã‚’ç¶™ç¶š
    let headerNumber = 0;
    const newlines = oldlines.map(line => {
        let l :string;
        if (line.indexOf("\#\#\# ") >= 0) {
            // h3ã‚°ãƒ«ãƒ¼ãƒ—
            headerNumber = 2;
            l = line.replace("### ", "\n\t\t");
        } else if (line.indexOf("\#\#") >= 0) {
            // h2ã‚°ãƒ«ãƒ¼ãƒ—
            headerNumber = 1;
            l = line.replace("## ", "\n\t");
        } else if (line.indexOf("\#") >= 0) {
            // h1ã‚°ãƒ«ãƒ¼ãƒ—
            headerNumber = 0;
            l = line.replace("# ", "\n");
        } else {
            // å‰è¡Œã®çŠ¶æ…‹ã‚’ç¶™ç¶š
            // headerNumberã¯å¤‰æ›´ã—ãªã„
            // headNumberåˆ†ã®Tabã‚’lineã®å‰ã«ä»˜ã‘ã‚‹
            l = "\t".repeat(headerNumber) + line;
        }
        return l;
    });
    const newLongText = nConcat(newlines);
    return newLongText;
}

/**
 * []ã‚’â—â—ã«å¤‰æ›´ã™ã‚‹
 * @param longText
 * @return æ–°ã—ã„ãƒ†ã‚­ã‚¹ãƒˆ
 */
const tag2maru = (longText: string) => {
    const oldlines = nSplit(longText);
    const newlines = oldlines.map(line => {
        const replaced = line.replace(new RegExp(/\[(.*)\]/), "â—$1â—\n");
        return replaced;
    });
    const newLongText = nConcat(newlines);
    return newLongText
}

/**
 * @summary "- " ã‚’ "ãƒ»\t"ã«ç½®æ›ã™ã‚‹
 * @param longText textareaå†…ã®æ–‡å­—åˆ—
 * @returns æ–°ã—ã„ãƒ†ã‚­ã‚¹ãƒˆ
 */
const hyphen2Circle = (longText: string) => {
    const oldlines = nSplit(longText);
    const newlines = oldlines.map(line => {
        return line.replace("- ", "ãƒ»\t");
    });
    const newLongText = nConcat(newlines);
    return newLongText;
}


/**
 * @summary åŠè§’ã‚¹ãƒšãƒ¼ã‚¹4ã¤ã‚’TABã«å¤‰æ›ã™ã‚‹ã€‚
 * @param longText textareaå†…ã®é•·æ–‡
 * @returns æ–°ã—ã„ãƒ†ã‚­ã‚¹ãƒˆ
 * @description æœ€åˆã«å‡ºã¦ããŸã‚¹ãƒšãƒ¼ã‚¹ã®ã¿ç½®æ›ã™ã‚‹
 */
const space2Tab = (longText: string) => {
    const oldlines = nSplit(longText);
    const newlines = oldlines.map(line => {
        const spans = spaceSplit(line);
        const newline = tConcat(spans);
        return newline;
    });
    const newLongText = nConcat(newlines);
    return newLongText;
};

/**
 * 
 * @param lineText 1è¡Œ
 * @returns åˆ†å‰²ã•ã‚ŒãŸè¡Œã®æ–­ç‰‡é…åˆ—
 */
const spaceSplit = (lineText: string) => {
    return lineText.split("    ");
};

/**
 * @summary æ–‡å­—åˆ—é…åˆ—ã®å…¨è¦ç´ ã‚’ã‚¿ãƒ–ã§çµåˆã™ã‚‹
 * @param spans æ–‡å­—åˆ—é…åˆ—
 * @returns çµåˆã•ã‚ŒãŸè¡Œæ–‡å­—åˆ—
 */
const tConcat = (spans: string[]) => {
    const longText = spans.join('\t');
    return longText;
};


/**
 * @summary å…±é€šå‡¦ç†ã€å…¥åŠ›æ–‡å­—åˆ—ã‚’æ”¹è¡Œã‚³ãƒ¼ãƒ‰ã§ã°ã‚‰ã™
 * @param longText é•·ã„æ–‡å­—åˆ—
 * @returns 1è¡Œã”ã¨ã«åˆ†ã‹ã‚ŒãŸæ–‡å­—åˆ—
 */
const nSplit = (longText: string) => {
    return longText.split("\n");
};

/**
 * @summary å…±é€šå‡¦ç†ã€å…¥åŠ›æ–‡å­—åˆ—é…åˆ—ã‚’æ”¹è¡Œã‚³ãƒ¼ãƒ‰ã§é€£çµã™ã‚‹
 * @param lines 1è¡Œã”ã¨ã®æ–‡å­—åˆ—é…åˆ—
 * @returns 1è¡Œã«ã¾ã¨ã‚ã‚‰ã‚ŒãŸæ–‡å­—åˆ—
 */
const nConcat = (lines: string[]) => {
    let longText = "";
    lines.forEach(line => {
        longText += line + "\n";
    });
    return longText;
};



/**
 * @summary ãƒ«ãƒ¼ãƒ«ã«å¾“ã„ãƒ†ã‚­ã‚¹ãƒˆã‚’å¤‰æ›ã™ã‚‹
 * @param longText
 * @returns ç½®æ›å¾Œã®ãƒ†ã‚­ã‚¹ãƒˆ
 */
const convert = (longText: string) => {
    const rule1Text = space2Tab(longText);
    const rule2Text = hyphen2Circle(rule1Text);
    const rule3Text = tag2maru(rule2Text);
    const rule4Text = sharpFormat(rule3Text);
    const newText = rule4Text;
    return newText;
};

export { convert };